<template>
  <div class="container">
	<my-header title="出险信息"></my-header>
	<section ref="top">
		<form>
			<div>
				<span>出险人姓名：</span>
				<div>
	  				<cube-input v-model="username" :disabled=true placeholder="请输入姓名"></cube-input>
				</div>
			</div>
			<div>
				<span>事故日期：&emsp;</span>
				<div>
					<date-picker :date='accidentDate' placeholder="请选择事故时间" @selectDate='selectDate'></date-picker>
				</div>
			</div>
			<div>
				<span>事故地点：&emsp;</span>
				<div class="addr">
          <cube-button @click="showSetDataPicker">
            <cube-input type="text" placeholder="省份" readonly v-model="provinceName"></cube-input>
          </cube-button>
          <cube-button @click="showSetDataPicker">
            <cube-input type="text" placeholder="市辖区" readonly v-model="cityName"></cube-input>
          </cube-button>
          <cube-button @click="showSetDataPicker">
            <cube-input type="text" placeholder="乡镇县" readonly v-model="countyName"></cube-input>
          </cube-button>
				</div>
			</div>
			<div>
				<span>&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</span>
				<div>
					<cube-input @blur="RESETINPUT" v-model="detaiAddr" placeholder="详细地址"></cube-input>
				</div>
			</div>
			<div>
				<span>事故经过：&emsp;</span>
				<div>
					<cube-textarea @blur="RESETINPUT" v-model="accidentPass" style="height:100px;" :maxlength=600 :autoExpand=true placeholder="请输入内容"></cube-textarea >
				</div>
			</div>
			<div>
				<span>案件性质：&emsp;</span>
				<div>
					<cube-radio-group v-model="nature" :hollow-style="true" :horizontal="true" :options="claimApply.claimNature" />
				</div>
			</div>
			<div>
				<span>理赔类型：&emsp;</span>
				<div>
					<cube-select v-model="claimTy" :options="claimApply.claimType"></cube-select>
				</div>
			</div>
			<div>
				<span>治疗医院：&emsp;</span>
				<div>
					<cube-input @blur="RESETINPUT" v-model="hospital" placeholder="请输入医院名称"></cube-input>
				</div>
			</div>
			<div>
				<span>联系方式：&emsp;</span>
				<div>
					<cube-input @blur="RESETINPUT" type="number" v-model="telephone" placeholder="请输入联系方式"></cube-input>
				</div>
			</div>
			<div>
				<span>资料递交机构：</span>
				<div class="level">
					<cube-button @click="showSetDataPicker">
            <cube-input type="text" placeholder="二级机构" @focus="asyncOrganPicker.show()" readonly v-model="twoLevel.label"></cube-input>
          </cube-button>
					<p></p>
          <cube-button @click="showSetDataPicker">
            <cube-input type="text" placeholder="三级机构" @focus="asyncOrganPicker.show()" readonly v-model="threeLevel.label"></cube-input>
          </cube-button>
				</div>
			</div>
	  	</form>
	  	<div>
			<foot-button @next="toApplicant"></foot-button>
		</div>
	</section>
  </div>
</template>
<script>
import Vue from 'vue';
import MyHeader from "@/components/model/MyHeader";
import DatePicker from "@/components/model/DatePicker";
import FootButton from "@/components/pages/claimApply/components/FootButton";
import {ISINPUTNULL,TELREG} from "@/utils/publicUtils";
import {INSUREPEOPLEINFO,QUERYIINSUREPEOPLEINFO} from "@/api";
import {provinceList, cityList, areaList} from '@/assets/js/area';
import {organData} from '@/assets/js/organ';
import {mapState,mapMutations} from 'vuex';
import {Style,createAPI,Input,Select,Textarea,CascadePicker,Radio,Button} from 'cube-ui';
Vue.use(CascadePicker);
const addressData = provinceList
addressData.forEach(province => {
  province.children = cityList[province.value]
  province.children.forEach(city => {
    city.children = areaList[city.value]
  })
});
export default {
  name: 'InsurePeople',
  data(){
  	return {
  		username:"",
  		accidentDate:"",
  		provinceName:"",
  		cityName:"",
  		countyName:"",
  		detaiAddr:"",
  		accidentPass:"",
      insuredBirth:"",
  		nature:"1",
  		claimTy:"",
  		hospital:"",
  		telephone:"",
      asyncOrganSelectedIndex:[0,0],
      asyncSelectedIndex:[0,0,0],
  		twoLevel:{
  			label:'',
  			value:''
  		},
  		threeLevel:{
  			label:'',
  			value:''
  		}
  	}
  },
  async created(){
  	let datas=await QUERYIINSUREPEOPLEINFO({claimNo:this.$route.query.claimNo});
    //此处为理赔报案跳转过来存储数据；
    if(!this.claimApply.claimtList){
      sessionStorage.setItem("claimtList",JSON.stringify(datas.data.data));
      this.claimApply.claimtList=JSON.parse(sessionStorage.getItem("claimtList",JSON.stringify(flag)));
    }
    if(!datas.data.data){
      datas.data.data=this.claimApply.claimtList;
      this.detaiAddr=datas.data.data.accidentAddr;
    }else{
      [this.provinceName,this.cityName,this.countyName,this.detaiAddr]=datas.data.data.accidentAddr.split('/');
    }
  	this.username=datas.data.data.insuredName;
  	this.accidentDate=datas.data.data.accidentTime;
  	this.accidentPass=datas.data.data.generalComm||"";
  	this.nature=datas.data.data.claimNature||"1";
  	this.claimTy=datas.data.data.claimType;
  	this.hospital=datas.data.data.hospitalid;
  	this.telephone=datas.data.data.insuredTel;
    this.insuredBirth=datas.data.data.insuredBirth;
    if(this.provinceName){
      provinceList.map((items,index)=>{
        if(items.text===this.provinceName){
          this.asyncSelectedIndex[0]=index;
          cityList[items.value].map((item,idx)=>{
            if(item.text===this.cityName){
              this.asyncSelectedIndex[1]=idx;
              areaList[item.value].map((it,i)=>{
                if(it.text===this.countyName){
                  this.asyncSelectedIndex[2]=i;
                }
              })
            }
          })
        }
      });
    };
    if(datas.data.data.accidentBranch){
      organData.forEach((item,index)=>{
        if(item.value===datas.data.data.accidentBranch){
          this.twoLevel.label=item.text;
          this.twoLevel.value=item.value;
          this.asyncOrganSelectedIndex[0]=index;
          item.children.forEach((it,i)=>{
            if(it.value===datas.data.data.accidentBranch3){
              this.threeLevel.label=it.text;
              this.threeLevel.value=it.value;
              this.asyncOrganSelectedIndex[1]=i;
            }
          })
        }
      })
    }
    this.asyncOrganPicker = this.$createCascadePicker({
      title: "请选择",
      async: true,
      data: organData,
      selectedIndex: this.asyncOrganSelectedIndex.slice(),
      onSelect: this.selectOrganHandle,
      //onCancel: this.cancelHandle,
      onChange: this.asyncOrganChangeHandle
    });
    
  },
  computed:{
	...mapState(['claimApply','claimType','claimNature','claimtList'])	
  },
  mounted(){
    this.setDataPicker = this.$createCascadePicker({
      title: '请选择',
      onSelect: this.selectHandle,
    });
  },
  methods:{
    ...mapMutations(['RESETINPUT']),
    showSetDataPicker(i, newIndex) {
      this.setDataPicker.show()
      this.setDataPicker.setData(addressData, this.asyncSelectedIndex)
    },
    asyncOrganChangeHandle(i, newIndex) {
      if (newIndex !== this.asyncOrganSelectedIndex[i]) {
        this.asyncOrganSelectedIndex[i] = newIndex;
        // If the first two column is changed, request the data for rest columns.
        if (i < 2) {
          // Mock async load.
          setTimeout(() => {
            if (i === 0) {
              const current = organData[newIndex];
              current.children = current.children;
              this.asyncOrganSelectedIndex[1] = 0;
            }
            this.asyncOrganPicker.setData(organData, this.asyncOrganSelectedIndex);
          }, 300)
        }
      }
    },
  	async toApplicant(){
  		ISINPUTNULL(this,this.accidentDate,"事故日期")&&ISINPUTNULL(this,this.provinceName,"事故地点")&&
      ISINPUTNULL(this,this.detaiAddr,"详细地址")&&ISINPUTNULL(this,this.accidentPass,"事故经过")&&
      ISINPUTNULL(this,this.claimTy,"理赔类型")&&ISINPUTNULL(this,this.hospital,"治疗医院")&&
      ISINPUTNULL(this,this.telephone,"联系方式")&&TELREG(this,this.telephone,"联系方式")&&
      ISINPUTNULL(this,this.twoLevel.label,"资料递交机构")&&
  		await INSUREPEOPLEINFO({
  			...this.$route.query,
        "openid":sessionStorage.getItem("openid"),
				"insuredName":this.username,
				"insuredSex":this.claimApply.claimtList.insuredSex,
        "insuredBirth":this.claimApply.claimtList.insuredBirth,
				"insuredCertType":this.claimApply.claimtList.insuredCertiType,
				"insuredCertCode":this.claimApply.claimtList.insuredCertiCode,
				"accidentTime":this.accidentDate,
				"accidentAddr":this.provinceName+"/"+this.cityName+"/"+this.countyName+"/"+this.detaiAddr.replace(/\//g,"\\"),
				"generalComm":this.accidentPass,
				"claimNature":this.nature,
				"claimType":this.claimTy,
				"hospitalid":this.hospital,
				"insuredTel":this.telephone,
				"accidentBranch":this.twoLevel.value,
				"accidentBranch3":this.threeLevel.value
			}).then((res)=>{
        sessionStorage.setItem("organ",this.threeLevel.label);
        sessionStorage.setItem("addr",this.provinceName+this.cityName+this.countyName+this.detaiAddr);
			  this.$router.push({path:this.$route.path+"/applicantpeople",query:Object.assign(this.$route.query,{organNo:this.threeLevel.value,insuredBirth:this.claimApply.claimtList.insuredBirth,claimType:this.claimTy})})
		  })
  	},
  	selectDate(val){
		  this.accidentDate=val;
	  },
    selectHandle(selectedVal, selectedIndex, selectedText) {
      this.asyncSelectedIndex=selectedIndex;
    	this.provinceName=selectedText[0];
    	this.cityName=selectedText[1];
    	this.countyName=selectedText[2];
    },
    selectOrganHandle(selectedVal, selectedIndex, selectedText) {
    	this.twoLevel={label:selectedText[0],value:selectedVal[0]};
    	this.threeLevel={label:selectedText[1],value:selectedVal[1]};
    }
  },
  components:{
  	MyHeader,DatePicker,FootButton,
  	CubeInput:Input,
  	CubeSelect:Select,
  	CubeTextarea:Textarea,
  	CubeRadioGroup:Radio,
    CubeButton:Button
  }
}
</script>
<style lang="stylus" scoped>
.container{
	height:100%;
	display:flex;
	flex-direction: column;
	&>section{
		flex:1;
		height:100%;
		overflow-y:auto;
		background:#fff;
		&>form{
			padding-left:10px;
			&>div{
				padding-top:16px;
				display:flex;
				align-items: center;
        >>>.cube-btn{
          width:100%;
          height: 100%;
          padding: 0;
        }
        >>>.cube-btn input{
          width: 100%;
          height: 100%;
          font-size: 14px;
          outline: none;
        }
				&>.addr{
					display:flex;
					>>>.cube-btn:nth-child(2){
						margin:0 10px;
					}
				}
				&>.level{
					width:235px;
					display:flex;
					&>div{
						flex:1;
					}
					p{
						width:10px;
					}
				}
				&>div{
					width:250px;
					>>>.cube-radio-group:after,>>>.cube-radio:after{
						border:0
					}
					>>>.cube-radio{
						padding-left:0;
						label{
							justify-content: left;
						}
					}
				}
			}
		}
	}
}
</style>



